import React, { useState } from 'react';
import { ethers } from "ethers";
import { CONTRACT_ADDRESS, CONTRACT_ABI } from "../config/contractConfig";

const DisplayData = () => {
  const [id, setId] = useState("");
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);

  const requestAccount = async () => {
    if (window.ethereum) {
      await window.ethereum.request({ method: "eth_requestAccounts" });
    } else {
      alert("MetaMask not found!");
    }
  };

  const getData = async (showLoading = true) => {
    if (!id) return;

    if (showLoading) setLoading(true);
    try {
      // Only request account if not already connected to avoid popup spam
      if (!window.ethereum.selectedAddress) {
        await requestAccount();
      }

      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const contract = new ethers.Contract(CONTRACT_ADDRESS, CONTRACT_ABI, provider);
      const result = await contract.getProductData(id);

      console.log("Fetched Data:", result);
      setData(result);
    } catch (error) {
      console.error("Error fetching data:", error);
    } finally {
      if (showLoading) setLoading(false);
    }
  };

  // Auto-refresh data every 5 seconds
  React.useEffect(() => {
    let interval;
    if (id) {
      getData(true); // Initial fetch
      interval = setInterval(() => {
        getData(false); // Silent background refresh
      }, 5000);
    }
    return () => clearInterval(interval);
  }, [id]);

  const [showHistory, setShowHistory] = useState(false);

  // Get latest data point
  const latestData = data && data.length > 0 ? data[data.length - 1] : null;

  return (
    <div className="min-h-screen bg-background p-8 flex flex-col items-center">
      <div className="w-full max-w-4xl">
        <h2 className="text-3xl font-bold mb-8 text-center text-white">
          Real-Time Environmental Monitor
        </h2>

        {/* Search Box */}
        <div className="bg-surface p-2 rounded-full border border-white/10 shadow-lg flex items-center max-w-md mx-auto mb-12">
          <input
            type="text"
            placeholder="Enter Product ID"
            value={id}
            onChange={(e) => setId(e.target.value)}
            className="flex-1 bg-transparent border-none outline-none text-white px-6 py-2 placeholder-gray-500"
          />
          <button
            onClick={() => getData(true)}
            className="bg-accent text-background p-3 rounded-full hover:bg-accent/90 transition-colors"
          >
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
          </button>
        </div>

        {loading && !data && (
          <div className="flex flex-col items-center justify-center mb-8">
            <div className="w-12 h-12 border-4 border-accent border-t-transparent rounded-full animate-spin mb-4"></div>
            <p className="text-gray-400">Connecting to sensor feed...</p>
          </div>
        )}

        {/* LIVE MONITOR BLOCK */}
        {latestData && (
          <div className="mb-12 animate-in fade-in slide-in-from-bottom-4">
            <div className="flex items-center justify-between mb-6">
              <h3 className="text-xl font-semibold text-white flex items-center gap-2">
                <span className="relative flex h-3 w-3">
                  <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-green-400 opacity-75"></span>
                  <span className="relative inline-flex rounded-full h-3 w-3 bg-green-500"></span>
                </span>
                Live Status
              </h3>
              <span className="text-sm text-gray-400 font-mono">
                Last Update: {latestData.timestamp ? new Date(parseInt(latestData.timestamp._hex) * 1000).toLocaleTimeString() : 'Just now'}
              </span>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              {/* Temperature Card */}
              <div className="bg-surface p-6 rounded-2xl border border-white/10 shadow-xl backdrop-blur-sm">
                <div className="flex items-center justify-between mb-4">
                  <span className="text-gray-400 text-sm uppercase tracking-wider">Temperature</span>
                  <span className="text-2xl"></span>
                </div>
                <div className="text-4xl font-bold text-white mb-1">
                  {parseInt(latestData.temp._hex)}C
                </div>
                <div className="text-sm text-gray-500">Optimal Range: 20-25C</div>
              </div>

              {/* Humidity Card */}
              <div className="bg-surface p-6 rounded-2xl border border-white/10 shadow-xl backdrop-blur-sm">
                <div className="flex items-center justify-between mb-4">
                  <span className="text-gray-400 text-sm uppercase tracking-wider">Humidity</span>
                  <span className="text-2xl"></span>
                </div>
                <div className="text-4xl font-bold text-white mb-1">
                  {parseInt(latestData.humidity._hex)}%
                </div>
                <div className="text-sm text-gray-500">Optimal Range: 30-50%</div>
              </div>

              {/* Heat Index Card */}
              <div className="bg-surface p-6 rounded-2xl border border-white/10 shadow-xl backdrop-blur-sm">
                <div className="flex items-center justify-between mb-4">
                  <span className="text-gray-400 text-sm uppercase tracking-wider">Heat Index</span>
                  <span className="text-2xl"></span>
                </div>
                <div className="text-4xl font-bold text-white mb-1">
                  {parseInt(latestData.hindex._hex)}C
                </div>
                <div className="text-sm text-gray-500">Feels like temperature</div>
              </div>
            </div>
          </div>
        )}

        {/* HISTORY TOGGLE */}
        {data && data.length > 0 && (
          <div className="w-full">
            <button
              onClick={() => setShowHistory(!showHistory)}
              className="w-full py-4 flex items-center justify-center gap-2 text-gray-400 hover:text-white transition-colors border-t border-white/10"
            >
              <span>{showHistory ? 'Hide History Log' : 'View Full History Log'}</span>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className={`h-5 w-5 transition-transform duration-300 ${showHistory ? 'rotate-180' : ''}`}
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            </button>

            {/* HISTORY TABLE */}
            {showHistory && (
              <div className="mt-6 bg-surface rounded-xl border border-white/10 overflow-hidden shadow-2xl animate-in fade-in slide-in-from-top-4">
                <div className="overflow-x-auto">
                  <table className="w-full text-left">
                    <thead>
                      <tr className="bg-black/40 border-b border-white/10">
                        <th className="p-4 text-sm font-semibold text-gray-300 uppercase tracking-wider">Sr. No.</th>
                        <th className="p-4 text-sm font-semibold text-gray-300 uppercase tracking-wider">Temperature</th>
                        <th className="p-4 text-sm font-semibold text-gray-300 uppercase tracking-wider">Humidity</th>
                        <th className="p-4 text-sm font-semibold text-gray-300 uppercase tracking-wider">Heat Index</th>
                        <th className="p-4 text-sm font-semibold text-gray-300 uppercase tracking-wider text-right">Time</th>
                      </tr>
                    </thead>
                    <tbody className="divide-y divide-white/5">
                      {data.slice().reverse().map((row, i) => ( // Show newest first
                        <tr key={i} className="hover:bg-white/5 transition-colors duration-200">
                          <td className="p-4 text-gray-400">{data.length - i}</td>
                          <td className="p-4 font-medium text-white">
                            <span className="flex items-center gap-2">
                               {parseInt(row.temp._hex)}C
                            </span>
                          </td>
                          <td className="p-4 font-medium text-white">
                            <span className="flex items-center gap-2">
                               {parseInt(row.humidity._hex)}%
                            </span>
                          </td>
                          <td className="p-4 font-medium text-white">
                            <span className="flex items-center gap-2">
                               {parseInt(row.hindex._hex)}C
                            </span>
                          </td>
                          <td className="p-4 text-gray-400 text-right font-mono text-sm">
                            {row.timestamp ? new Date(parseInt(row.timestamp._hex) * 1000).toLocaleString() : 'N/A'}
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default DisplayData;
